import React from 'react';
import { connect } from 'react-redux';
import './login.scss';
import config from '../../../config';
import AnimatedInput from '../../Molecules/AnimatedInput';
import Button from '../../Atoms/Button';
import { Allimages } from '../../../Utils/imageRetriver';
import { userActions } from '../../../Store/login/user.action';
import { removeAccessToken, removeAuthorization } from '../../../auth/auth';

class LoginPage extends React.Component {
  constructor(props) {
    super(props);

    // reset login status
    // this.props.logout();

    this.state = {
      username: '',
      password: '',
      submitted: false,
    };
    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleChange(event) {
    const { id, value } = event.target;
    this.setState({ [id]: value });
  }

  handleSubmit(e) {
    e.preventDefault();

    this.setState({ submitted: true });
    const { username, password } = this.state;
    if (username && password) {
      this.props.login({ username, password, grant_type: 'password' });
    }
  }
  componentDidUpdate() {
    // if (!this.props.isLoggedIn) removeAuthorization();
    // if (this.props.isLoggedIn) {
    //   browserHistory.push('/flightManagement');
    // }
  }
  componentDidMount() {
    console.log(this.props,"loginprops")
    removeAuthorization();
    removeAccessToken();
  }
  googlelogin=()=>{
    console.log("from google")
   localStorage.setItem('Googlelogin','true')
    // this.props.loginmethod(true)
    // window.location.href = "https://accounts.google.com/logout?"
  }
  render() {
    console.log(this.props.isLoadingLogin ,"propsfromlogin");
    const { logo, login } = config;
    const { isLoadingLogin } = this.props;
    const { username, password, submitted } = this.state;
    return (
      <div
        className="loginContainer"
        style={{ backgroundImage: login.bgColor }}
      >
        
        <div className="login-box" style={{ backgroundImage: login.loginBg }}>
          <div className="logo-container">
            <div className="logo-container-inner">
              <img
                className="logo-img"
                alt="logo"
                src={Allimages[logo.img]}
                srcSet={`${Allimages[logo.img2x]}, ${Allimages[logo.img3x]}`}
              />
            </div>
          </div>
          <div className="login-box-inner">
            <div className="user-area">
              <form onSubmit={this.handleSubmit}>
                <div className="user-input-area">
                  <AnimatedInput
                    className="user-name"
                    id="username"
                    label="Username"
                    onChange={this.handleChange}
                  />
                  {submitted && !username && (
                    <div className="help-block">{login.err.username}</div>
                  )}
                </div>
                <div className="password-area">
                  <AnimatedInput
                    className="password-name"
                    type={'password'}
                    id="password"
                    label="Password"
                    onChange={this.handleChange}
                  />
                  {submitted && !password && (
                    <div className="help-block">{login.err.passwordmsg}</div>
                  )}
                </div>
                <div >
                <div className="submit-area " >
                  <Button
                    className="submit-button"
                    ButtonHandler={this.handleSubmit}
                  >
                    {isLoadingLogin ? <Loading /> : 'Login'}{' '}
                  </Button>
                </div>
               
               <div style={{'textAlign':'center','margin-top':'5px'}}>(or)</div> 
                <div style={{'padding-top':'10px'}}>
                  <div className="loginwithgoogle" onClick={this.googlelogin}>
                  <a
                    target="_self"
                    style={{'color':'white','textDecoration':'none'}}
                    href = "https://redsmart.airasia.com/redsmart/auth?url=https://redsmart.airasia.com/redirect"
                    // ButtonHandler={this.handleSubmit}
                  >
                   
                    {/* {isLoadingLogin ? <Loading /> :*/}
                       Login With Google{' '} 
                  </a>
                  </div>
                </div>
                </div>
              </form>
              <div className="forgot-pass">
                {/* <button href="#" className="forgot-pass-link">
                  Forgot Password?
                </button> */}
              </div>
            </div>
          </div>
        </div>
        <div className="copy-right">
          <div>Copyright © 2020 Tata Consultancy Services Limited. All Rights Reserved.</div><br/>
          <div >The Application is Best viewed at 1366 * 768 Resolution with Chrome 4.0 +</div>
        </div>
      </div>
    );
  }
}

const Loading = () => (
  <img
    alt="loader"
    src="data:image/gif;base64,"
  />
);

function mapState(state) {
  const { isLoggedIn, isLoadingLogin } = state.authentication;
  return { isLoggedIn, isLoadingLogin };
}

const actionCreators = {
  login: userActions.login,
  logout: userActions.logout,
  // loginmethod:userActions.loginmethod,
};

export default connect(mapState, actionCreators)(LoginPage);
